import { mapState } from "vuex";

export default {
    data() {
        return {
            mch_id: null,
            type: '',
            first: true,
            value: -1,
            color: '',
            list: [],
        }
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            adminImg: state => state.mallConfig.__wxapp_img.app_admin
        })
    },
    methods: {
        editCat(item) {
            let id = item.value ? item.value : null;
            uni.navigateTo({
                url: `/pages/goods-edit/cat-edit/cat-edit?mch_id=`+this.mch_id+`&type=`+this.type+`&id=`+ id
            })
        },
        toggle(item) {
            item.show = !item.show;
            this.$forceUpdate();
        },
        getCat() {
            let that = this;
            let url = this.mch_id == 0 ? that.$api.app_admin.cat : that.type == 'mall' ? that.$api.mch.cat : that.$api.mch.mch_cat;
            let para;
            that.$request({
                url: url,
                data: {
                    mch_id: that.mch_id
                }
            }).then(response=>{
                that.first = false;
                that.$hideLoading();
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                    for(let item of that.list) {
                        item.show = false;
                        if(item.children) {
                            for(let children of item.children) {
                                children.show = false;
                            }
                        }
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },
        pop(value) {
            this.value = value;
        },
        close() {
            this.value = -1;
        },
        toDelete() {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '删除中...',
            });
            let url = this.mch_id == 0 ? that.$api.app_admin.cat_destroy : that.$api.mch.mch_cat_destroy;
            let para;
            that.$request({
                url: url,
                data: {
                    id: this.value,
                    mch_id: that.mch_id
                },
                method: 'post'
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    this.value = -1;
                    this.getCat();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
    },

    onShow() {
        if(!this.first) {
            uni.showLoading({
                mask: true,
            });
            this.getCat();
        }
    },

    onLoad(options) {
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        this.type = options.type ? options.type : '';
        this.mch_id = options.mch_id ? options.mch_id : 0;
        this.color = this.mch_id > 0 ? '#ff4544' : '#446dfd';
        that.getCat();
    }
}
